body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
    text-align: center;
    background: white;
}
.snakeBoardWrapper {
    position: relative;
    display: inline-block;
    margin: 20px -20px 0px 100px;
    overflow: hidden;
    right: 10%;
}

.snakeBoard {
    display: table;
    border-collapse: collapse;
}

.snakeBoardRow {
    display: table-row;
}

.snakeBoardCell {
    display: table-cell;
    border: 1px solid black;
    background-color: whitesmoke;
}

.snakeBody {
    background: #333;
    position: absolute;
}
.snakeFood {
    background: #7cb342;
    position: absolute;
}
.sliderComponent {
    float: left;
    display: inline-block;
    margin-top: -100px;
}

.rangeSlider {
    position: relative;
}

.sliderValue {
    position: relative;
    font-family: 'Courier';
    font-size: 15px;
}

.sliderLabel {
    position: relative;
    font-family: 'Courier';
    font-size: 15px;
}


* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

.activeSnakeContainer {
    border: 20px outset #999990;
    min-height: 100vh;
}

.snakeContainer {
    border: 20px outset #999990;
    min-height: 100vh;
}

.snakeTitle {
    margin: 20px;
    font-family: 'Courier';
    font-size: 60px;
}

.snakeHighScore {
    margin: 10px;
    font-family: 'Courier';
    font-size: 25px;
}

.snakeCurrentScore {
    margin: 10px;
    font-family: 'Courier';
    font-size: 25px;
}

.snakeHelp {
	margin: 30px;
    font-family: 'Courier';
    font-size: 20px;
}

.snakeGame {
    display: inline-block;
    position: relative;
}

.sliderComponent {
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
}



.borderlessSwitch {
    display: inline-block;
    flex-direction: inherit;
    position: relative;
    font-family: Courier;
    font-size: 15px;
}

.button {
    font-family: 'Zorque';
    font-size: 20px;
    border: none;
    background-color: white;
    border: 5px outset #999990;
    margin: 50px 20px 20px 20px;
    padding: 5px;
}

